.header {
    padding: 0 5%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.container {
    flex-grow: 1;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    border-radius: 10px;
    padding: 2px;
    background-color: #222222;

}
.container button {
    flex-grow: 1;
    text-transform: uppercase;
    font-size: .8rem;
    height: 100%;
    position: relative;
    z-index: 1;
    color: #dddddd;
}
#selected {
    display: block;
    transition: .5s;
    background-color: #3e3e3e;
    border-radius: 8px;    
    padding: 4px 8px;
}
.header > button {
    font-size: .9rem;
}

.remove_btn {
    color: #ff6060;
    word-spacing: 1px;
}