.container {
    width: 100%;
    max-height: 100vh;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #0e0e0e;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    overflow-y: auto;
    transform: translateY(150%);
    opacity: .5;
    transition: .6s;
}
#open {
    transform: translateY(0);   
    opacity: 1;
}
.header {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    line-height: 23px;
    padding: 10px 5%;
    font-size: .85rem;
    color: #aaaaaa;
    word-spacing: 2px;
}
.body {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 15px 5%;
}